import CONFIG from "@/game/config";

let zh_cn = {
    "wood": "木头",
    "metal": "金属",
    "rope": "绳子",
    "stone": "石头",
    "clay": "粘土",
    "worktable": "工作台",
    "storage": "仓库",
    "farm": "农田",
    "health": "健康",
    "hunger": "饥饿",
    "thirsty": "口渴",
    "river": "河流",
    "forest": "森林",
    "cave": "岩洞",

    "initialized": "初始化完成",
    "return_home": "回家咯",
    "return_home_for_hunger": "太饿了，回家了",
    "empty_storage": "你的仓库是空的",
    "you_have": "你有：${list}",
    "start_explore": "开始探索",
    "get_resources": "获得资源：${list}",
    "nothing_found": "什么都没找到",
    "found_terran": "找到：${terran}",
    "item": "${id} * ${amount}",
    "status": "健康：${health}，饥饿：${hunger}",
    "time": "时间：${time}",
    "save_done": "保存完成",
    "load_done": "载入完成",
    "load_failed": "载入失败",
};

const replaceRegExp = /\${\s*(\w+)\s*}/g;

function makeReplacer(env) {
    return (match, key) => {
        let r = env[key];
        if (!r) {
            return key;
        }
        switch (typeof r) {
            case 'string': return translate(r, env);
            case 'number': return Number.isInteger(r) ? r.toString() : r.toFixed(CONFIG.PRECISION);
            default: return String(r);
        }
    };
}

function replaceKeys(rowStr, env) {
    return rowStr.replace(replaceRegExp, makeReplacer(env));
}

function translate(translationKey, env = {}) {
    let t = zh_cn[translationKey];
    return t ? replaceKeys(t, env) : translationKey;
}

export {
    translate,
}