import {check, Dice, limit, random} from "@/util/number";
import CONFIG from "@/game/config";
import {translate} from "@/game/lang";
import {addItemsTo} from "@/util/item-manager";
import {ArgMapper} from "@/util/command-manager";

function onInitializing(game) {
    let profile = game.profile;

    profile.health = 100;
    profile.hunger = 0;
    profile.inverntory = [];
    profile.storage = [];
    profile.working = null;

    game.sendOutput(`初始化完成！`);

    return 'waiting';
}

function onWaiting({profile}) {
    profile.health = limit(profile.health + 1, 0, 100);
    profile.hunger = limit(profile.hunger - 1, 0, 100);
}

function onExploring(game) {
    game.profile.hunger += 1;
    if (check(CONFIG.EXPLORE_RATE)) {
        game.profile.working = Dice.from(game.registry.getData('terran'), 'id', 'weight').roll();
        game.sendOutput('found_terran', false, {terran: game.profile.working});
        return 'mining';
    }
}

function onMining(game) {
    let profile = game.profile;
    profile.hunger += 2;
    if (check(CONFIG.MINE_RATE)) {
        let resources = game.registry.getData('terran', profile.working, 'resources');
        let result = [];
        resources.forEach(({id, chance, min, max}) => {
            if (check(chance)) {
                result.push({id: id, amount: random(max, min)});
            }
        });

        if (result.length === 0) {
            game.sendOutput('nothing_found');
            return 'mining';
        } else {
            addItemsTo(profile.storage, ...result);
            let list = result.map(item => translate('item', item)).join('、');
            game.sendOutput('get_resources', false, {list});
            game.sendOutput('return_home');
            return 'waiting';
        }
    }
}

function onPostTick(game) {
    let profile = game.profile;
    profile.health = limit(profile.health, 0, 100);
    profile.hunger = limit(profile.hunger, 0, 100);
    if (CONFIG.DEBUG) {
        console.log(profile);
    }

    if (profile.health <= 0) {
        game.sendOutput('你死了');
    }

    if (profile.state === 'waiting') {
        return;
    }

    if (profile.health < 10) {
        profile.state = 'waiting';
        game.sendOutput('你血太少，自动回家了');
    }

    if (profile.hunger > 90) {
        profile.state = 'waiting';
        game.sendOutput('return_home_for_hunger');
    }
}

function setup(game) {
    game.title = 'Farorb';

    let register = game.registry.register.bind(game.registry);
    let reg = game.registry.reg.bind(game.registry);

    // region 注册事件监听器
    reg('listener',
        {id: 'initializing', value: onInitializing},
        {id: 'waiting', value: onWaiting},
        {id: 'exploring', value: onExploring},
        {id: 'mining', value: onMining},
        {id: 'post-tick', value: onPostTick},
    );
    // endregion

    // region 注册命令
    reg('command',
        {header: 'list', action: list},
        {header: 'explore', action: explore},
        {header: 'debug', action: debug},
        {header: 'say', action: say, mapper: new ArgMapper().then('text', ArgMapper.REG_STRING, r => r[1])},
        {header: 'status', action: status},
        {header: 'home', action: home},
    );
    // endregion

    // region 注册所有材料
    register('material', 'dict',
        {id: 'wood'},
        {id: 'metal'},
        {id: 'rope'},
        {id: 'stone'},
        {id: 'clay'},
    );
    // endregion

    // region 注册所有建筑
    register('building', 'dict',
        {id: 'worktable'},
        {id: 'storage'},
        {id: 'farm'},
    );
    // endregion

    // region 注册所有状态
    register('status', 'dict',
        {id: 'health', min: 0, max: 100, def: 100},
        {id: 'hunger', min: 0, max: 100, def: 0},
        {id: 'thirsty', min: 0, max: 100, def: 0},
    );
    // endregion

    // region 注册所有合成表
    register('struct', 'dict',
        {id: 'worktable', materials: [{id: 'wood', amount: 10}, {id: 'metal', amount: 2}]},
        {id: 'storage', materials: [{id: 'wood', amount: 25}]},
    );
    // endregion

    // region 注册地形
    register('terran', 'dict',
        {id: 'river', weight: 10, total: 100, resources: [{id: 'clay', chance: 0.35, min: 4, max: 25}]},
        {id: 'forest', weight: 70, total: 100, resources: [{id: 'wood', chance: 0.75, min: 10, max: 20}, {id: 'rope', chance: 0.15, min: 1, max: 5}]},
        {id: 'cave', weight: 20, total: 100, resources: [{id: 'stone', chance: 0.80, min: 5, max: 15}, {id: 'metal', chance: 0.30, min: 2, max: 5}]},
    );
    // endregion


}

function list(args, header, game) {
    if (game.profile.storage.length === 0) {
        game.sendOutput('empty_storage');
    } else {
        let list = game.profile.storage.map(item => translate('item', item)).join('、');
        game.sendOutput('you_have', false, {list});
    }
}

function explore(args, header, game) {
    game.profile.state = 'exploring';
    game.sendOutput('start_explore');
}

function home(args, header, game) {
    game.profile.state = 'waiting';
    game.sendOutput('return_home');
}

function debug() {
    CONFIG.DEBUG = !CONFIG.DEBUG;
}

function say({text}, header, game) {
    game.sendRawOutput(text);
}

function status(args, header, game) {
    game.sendOutput('status', false, game.profile);
}

export {
    setup,
};