import CONFIG from "@/game/config";
import Ticker from "@/util/ticker";
import {CommandManager} from "@/util/command-manager";
import {setup} from "@/game/setup";
import Registry from "@/util/registry";
import {nextUID} from "@/util/number";
import {translate} from "@/game/lang";



const GAME = {
    title: '',
    profile: {
        state: 'initializing',
        time: 0,
    },
    commandManager: new CommandManager(),
    ticker: new Ticker(),
    registry: new Registry(),
    output: [],

    /**
     * 启动定时器
     * @returns {boolean}
     */
    start() {
        this.ticker.task = this.tick.bind(this);
        this.ticker.period = CONFIG.TICK_PERIOD;
        return this.ticker.run();
    },

    /**
     * 停止定时器
     * @returns {boolean}
     */
    stop() {
        this.ticker.stop();
        return !this.ticker.running;
    },

    /**
     * 执行指令
     * @param str 指令字符串
     */
    exec(str) {
        this.commandManager.execute(str, this);
    },

    tick() {
        this.profile.time++;
        this.profile.state = this.trigger(this.profile.state) || this.profile.state;
        this.trigger('post-tick');
    },

    /**
     * 触发监听器
     * @param event 事件
     * @returns {null|*} 监听器的返回值
     */
    trigger(event) {
        let listener = this.registry.getData('listener', event);
        return listener ? listener(this) : null;
    },

    sendOutput(msg, fromPlayer = false, env) {
        this.output.push({
            uid: nextUID(),
            fromPlayer: fromPlayer,
            content: translate(msg, env),
        });
    },

    sendRawOutput(msg, fromPlayer = false) {
        this.output.push({
            uid: nextUID(),
            fromPlayer: fromPlayer,
            content: msg,
        });
    },
};

GAME.registry.create('command', Registry.LIST);
GAME.registry.create('listener', Registry.MAP);

GAME.registry.reg('command',
    {header: 'save', action: (args, header, game) => {
        localStorage.setItem(CONFIG.LOCAL_STORAGE_KEY, JSON.stringify(game.profile));
        game.sendOutput('save_done');
    }},
    {header: 'load', action: (args, header, game) => {
        let s = JSON.parse(localStorage.getItem(CONFIG.LOCAL_STORAGE_KEY));
        if (s) {
            Object.assign(game.profile, s);
            game.sendOutput('load_done');
        } else {
            game.sendOutput('load_failed');
        }
    }},
);


setup(GAME);

GAME.registry.getData('command').forEach(({header, action, mapper}) => {
    GAME.commandManager.add(header, action, mapper);
});



export {
    GAME,
}

