
class ArgMapper {

    static REG_STRING = /"([^"]*)"/;
    static REG_INT = /\d+/;
    static REG_FLOAT = /\d*\.\d+/;
    static REG_PERCENT = /((\d*\.)?\d+)%/;
    static REG_NUMBER = /(\d*\.)?\d+/;

    constructor() {
        this.items = [];
    }

    then(name, regExp, converter = null) {
        this.items.push({name, regExp, converter});
        return this;
    }

    map(rowStr) {
        let args = {};
        let str = rowStr;
        for (let {name, regExp, converter} of this.items) {
            let result = regExp.exec(str);
            if (result) {
                args[name] = converter ? converter(result) : result[0];
                str = str.substring(result.index + result[0].length);
            } else {
                return  null;
            }
        }
        return args;
    }
}

class CommandManager {

    constructor() {
        this.commands = {};
        this.headerMatcher = /^[A-Za-z_0-9]+/;
    }

    execute(rowStr, env) {
        let headerResult = this.headerMatcher.exec(rowStr);
        if (!headerResult) {
            return false;
        }
        let header = headerResult[0];
        let argsStr = rowStr.substring(headerResult.index + header.length);
        let command = this.commands[header];
        if (!command) {
            return false;
        }
        let args = command.argMapper ? command.argMapper.map(argsStr) : {};
        return command.action(args, header, env);
    }

    add(header, action, argMapper) {
        this.commands[header] = {header, action, argMapper};
    }

}

export {
    CommandManager,
    ArgMapper,
};