/**
 * 权重骰子
 * 根据每个项的权重返回对应的键
 * 若最终结果没有落在键值对范围内，则返回定义的默认值
 */
class Dice {

    /**
     * 从一个数据集产生新的骰子
     * @param dataSet 数据集
     * @param keyName 键名
     * @param valueName 值名
     * @param def 默认值
     * @returns {Dice} 新生成的骰子
     */
    static from(dataSet, keyName, valueName, def = null) {
        let dice = new Dice(def);
        for (let data of dataSet) {
            dice.put(data[keyName], data[valueName]);
        }
        return dice;
    }

    /**
     * 构建器
     * @param def 默认值
     */
    constructor(def = null) {
        this.total = 0;
        this.def = def;
        this.map = {};
    }

    /**
     * 投掷骰子
     * @returns {string|*} 骰子的结果
     */
    roll() {
        let target = Math.random() * this.total;
        let num = 0;
        for (let key of Object.keys(this.map)) {
            num += this.map[key];
            if (target < num) {
                return key;
            }
        }
        return this.def;
    }

    /**
     * 清空数据
     */
    clear() {
        this.map = {};
        this.total = 0;
    }

    /**
     * 加入新的键值对
     * @param key
     * @param value
     */
    put(key, value) {
        if (this.map[key]) {
            this.total -= this.map[key];
        }
        this.map[key] = value;
        this.total += value;
    }

    /**
     * 获取键所对应的值
     * @param key
     * @returns {*|number}
     */
    get(key) {
        return this.map[key] || 0;
    }
}

/**
 * 在给定范围内选取一个随机数
 * @param max 最大值（不包括）
 * @param min 最小值（包括）
 * @returns {number} 随机值
 */
function rand(max = 100, min = 0) {
    return Math.floor(Math.random() * (max - min) + min);
}

/**
 * 在给定范围内选取一个浮点数
 * @param max 最大值（不包括）
 * @param min 最小值（包括）
 * @returns {number} 随机值
 */
function random(max = 100, min = 0) {
    return Math.random() * (max - min) + min;
}

/**
 * 根据可能性进行投掷
 * @param possibility 可能性
 * @returns {boolean} 结果
 */
function check(possibility) {
    return Math.random() < possibility;
}

let uid = Date.now();

/**
 * 生成游戏中独一无二的UID
 * @returns {number} 生成的UID
 */
function nextUID() {
    return uid++;
}

/**
 * 限制数值大小
 * @param value 原始数值
 * @param min 最小值
 * @param max 最大值
 * @returns {number} 限制的结果
 */
function limit(value, min, max) {
    if (value < min) {
        return min;
    } else {
        return Math.min(value, max);
    }
}

export {
    Dice,
    rand,
    random,
    nextUID,
    check,
    limit,
};