<template>
    <div class="game-main" ref="main">
        <div class="header shadow">
            <p class="title">{{ title }}</p>
        </div>

        <transition-group tag="div" class="output" enter-active-class="animated fadeIn">
            <p v-for="msg of output" :key="msg.uid" :class="msg.fromPlayer ? 'from-player' : 'from-other'">{{ msg.content }}</p>
        </transition-group>

        <div class="footer shadow">
            <input class="shadow" ref="input" type="text" v-model="input" @keydown="handleTypeInput($event)"/>
            <button class="shadow" :disabled="input === ''" @click="finishInput">执行</button>
        </div>
    </div>
</template>

<script>
    import 'animate.css';
    import {GAME} from "@/game/core";

    export default {
        name: "Farorb",
        data() {
            return {
                output: GAME.output,
                input: '',
                inputHistory: [],
                inputHistoryIndex: 0,
                inputCache: '',
            };
        },
        watch: {
            output() {
                this.$nextTick(this.scrollToBottom);
            }
        },
        computed: {
            canFinish() {
                return !/^\s*$/.test(this.input);
            },
            title() {
                return GAME.title;
            },
        },
        methods: {
            handleTypeInput(event) {
                if (event.key === 'Enter' && this.canFinish) {
                    this.finishInput();
                } else if (event.key === 'ArrowUp') {
                    this.tryReplaceWithHistory(-1);
                } else if (event.key === 'ArrowDown') {
                    this.tryReplaceWithHistory(1);
                }
            },
            tryReplaceWithHistory(offset) {
                if (this.inputHistory.length === 0) {
                    return;
                }
                if (this.inputHistoryIndex === this.inputHistory.length) {
                    this.inputCache = this.input;
                }

                this.inputHistoryIndex += offset;
                if (this.inputHistoryIndex === this.inputHistory.length) {
                    this.input = this.inputCache;
                } else if (this.inputHistoryIndex >= 0 && this.inputHistoryIndex < this.inputHistory.length) {
                    this.input = this.inputHistory[this.inputHistoryIndex];
                } else if (this.inputHistoryIndex < 0) {
                    this.input = this.inputCache;
                    this.inputHistoryIndex = this.inputHistory.length;
                } else {
                    this.inputHistoryIndex = 0;
                    this.input = this.inputHistory[0];
                }
                this.$nextTick(() => this.$refs.input.setSelectionRange(this.input.length, this.input.length));
            },
            finishInput() {
                GAME.sendRawOutput(this.input, true);
                GAME.exec(this.input);
                if (!this.inputHistory.length || this.inputHistory[this.inputHistory.length - 1] !== this.input) {
                    this.inputHistory.push(this.input);
                }
                this.inputHistoryIndex = this.inputHistory.length;
                this.inputCache = '';
                this.input = '';
            },
            scrollToBottom() {
                let output = this.$refs.main.getElementsByClassName('output')[0];
                output.scrollTop = output.scrollHeight;
            },
        },
        mounted() {
            GAME.start();
        },
        beforeDestroy() {
            GAME.stop();
        }
    }
</script>

<style scoped>
    .game-main {
        width: 100%;
        height: 100%;
        padding: 0em;
        box-sizing: border-box;

        display: flex;
        flex-direction: column;
    }

    .output {
        width: 100%;
        margin: 1.5em 0;
        padding: 0 2em;
        box-sizing: border-box;

        flex: 1;

        overflow-y: auto;
    }

    .output > p {
        margin: 0;
        padding: .5em 1em;
        border-bottom: #cccccc solid .05em;
    }

    .message-list {
        width: 100%;
        height: 100%;
    }

    .from-player {
        text-align: right;
    }

    .from-player::after {
        content: ' <<<';
        color: #bfbfbf;
    }

    .from-other::before {
        content: '>>> ';
        color: #bfbfbf;
    }

    .header {
        width: 100%;
        padding: 1em 1em;
        box-sizing: border-box;

        text-align: center;
        color: #2c3e50;
        font-family: Consolas, sans-serif;
    }

    .title {
        width: 100%;
        padding: 0;
        margin: 0;
        box-sizing: border-box;

        font-size: 2em;
        font-weight: bold;
    }

    .time {
        width: 100%;
        padding: 0;
        margin: 0;
        box-sizing: border-box;

        font-size: 1em;
    }

    .footer {
        width: 100%;
        padding: 1em 1em;
        box-sizing: border-box;

        display: flex;
        flex-direction: row;
    }

    .footer > input {
        flex: 1;
    }
</style>

<style>

    .shadow {
        box-shadow: #cccccc 0 0 .2em;
    }

    button, input {
        font-size: 1em;
        margin: 0 .5em;
        padding: .3em 1.5em;
        outline: none;
        border-radius: 2em;
        border: none;

        font-family: Consolas, "Microsoft YaHei UI", Avenir, Helvetica, Arial, sans-serif;
    }

    button {
        background-color: #2c3e50;
        color: #FFFFFF;

        white-space: nowrap;
    }

    button:hover {
        background-color: #466380;
    }

    button:active {
        background-color: #547799;
    }

    button:disabled {
        background-color: #cccccc;
        color: #808080;
    }

    input {
        color: #808080;
    }

</style>