

class Registry {

    static DICT = 'dict';
    static MAP = 'map';
    static LIST = 'list';

    constructor() {
        this.registrations = {};
    }

    /**
     * 清空注册表，这会导致所有数据的丢失
     */
    clear() {
        this.registrations = {};
    }

    ensureCatalogExist(catalog, type) {
        let r = this.registrations[catalog];
        if (!r) {
            let data;
            switch (type) {
                case 'dict':
                    data = {};
                    break;
                case 'map':
                    data = {};
                    break;
                case 'list':
                    data = [];
                    break;
            }
            r = {type, data};
            this.registrations[catalog] = r;
        }
        return r;
    }

    /**
     * 注册一系列注册项
     * 若type为'dict'，则values的元素为带有id字段的object
     * 若type为'map'，则values的元素为带有id与value字段的object
     * 若type为'list'，则values的元素为任意值
     * @param catalog 类目
     * @param type 注册类型
     * @param values 注册项列表
     */
    register(catalog, type, ...values) {
        let data = this.ensureCatalogExist(catalog, 'dict').data;
        switch (type) {
            case 'dict':
                values.forEach(entry => {
                    data[entry.id] = entry;
                });
                break;
            case 'map':
                values.forEach(entry => {
                    data[entry.id] = entry.value;
                });
                break;
            case 'list':
                data.push(...values);
                break;
        }
    }

    /**
     * 创建注册条目
     * @param catalog 类目
     * @param type 类型
     */
    create(catalog, type) {
        return this.ensureCatalogExist(catalog, type);
    }

    /**
     * 注册一系列值，但是注册条目必须已经创建
     * 根据type的不同，value有不同的要求
     * 若type为'dict'，则values的元素为带有id字段的object
     * 若type为'map'，则values的元素为带有id与value字段的object
     * 若type为'list'，则values的元素为任意值
     * @param catalog 类目
     * @param values 注册项列表
     */
    reg(catalog, ...values) {
        let r = this.registrations[catalog];
        if (r) {
            this.register(catalog, r.type, ...values);
        }
    }



    /**
     * 获取注册项
     * 所查类目的type为dict时，若id为空则以[]形式返回所有的注册项，否则根据id查询注册项，若field不为空，则自动取出注册项的field字段的值
     * 所查类目的type为map时，若id为空则以[]形式返回所有的注册值，否则根据id查询注册项，无视field
     * 所查类目的type为list时，若id为空则以[]形式返回所有的注册项
     * @param catalog 类目
     * @param id 所查询的ID，可以为空
     * @param field 查询项的字段，可以为空
     * @returns {null|[]|*} 注册项
     */
    getData(catalog, id = null, field = null) {
        let r = this.registrations[catalog];
        if (!r) {
            return null;
        }
        let {type, data} = r;
        switch (type) {
            case 'dict': {
                if (!id) {
                    return Object.values(data);
                }
                let value = data[id];
                return (value && field) ? value[field] : value;
            }
            case 'map':
                return id ? data[id] : Object.values(data);
            case 'list':
                return data;
        }
    }

}

export default Registry;