/**
 * 向容器放入单堆物品
 * @param container 容器
 * @param item 物品
 */
function addItemTo(container, item) {
    let {id, amount} = item;
    let itemStack = container.find(e => e.id === id);
    if (!itemStack) {
        itemStack = {id: id, amount: 0};
        container.push(itemStack);
    }
    itemStack.amount += amount;
}

/**
 * 从容器中去取出单堆物品
 * @param container 容器
 * @param item 物品
 * @returns {null|{amount: number, id: string}} 去除的物品，若未成功取出，则返回null
 */
function takeItemFrom(container, item) {
    let {id, amount} = item;
    let itemStack = container.find(e => e.id === id);
    if (!itemStack || itemStack.amount < amount) {
        return null;
    }
    itemStack.amount -= amount;
    if (itemStack.amount === 0) {
        let index = container.indexOf(itemStack);
        container.splice(index, 1);
    }
    return {id, amount};
}

/**
 * 向容器放入多堆物品
 * @param container 容器
 * @param items 物品列表
 */
function addItemsTo(container, ...items) {
    items.forEach(e => addItemTo(container, e));
}

/**
 * 从容器中去取出多堆物品
 * @param container 容器
 * @param items 物品列表
 * @returns {{amount: number, id: string}[]} 成功取出的物品列表
 */
function takeItemsFrom(container, ...items) {
    return items.map(item => takeItemFrom(container, item)).filter(item => item);
}

export {
    addItemTo,
    takeItemFrom,
    addItemsTo,
    takeItemsFrom,
}