/**
 * 可以复用的计时器
 */
class Ticker {
    /**
     * 构造器
     * @param task 计时任务
     * @param period 计时间隔
     */
    constructor(task = null, period = 1000) {
        this.task = task;
        this.period = period;
        this.pid = null;
    }

    /**
     * 检查计时器是否在运行
     * @returns {boolean} 运行状态
     */
    get running() {
        return this.pid !== null;
    }

    /**
     * 若任务没在运行，则开始计时
     * @returns {boolean} 是否正在运行
     */
    run() {
        if (this.pid === null && this.task) {
            this.pid = setInterval(this.task, this.period);
        }
        return this.running;
    }

    /**
     * 若任务正在运行，则停止计时
     */
    stop() {
        if (this.pid !== null) {
            clearInterval(this.pid);
        }
    }

}

export default Ticker;